// STATE
const state = {
    textOn: true,
    sidebarLinks: [],
    theme: 'light'
}

// GETTERS
const getters = {
    textOnStatus: state => {
        return state.textOn;
    },

    getSidebarLinks: state => {
        return state.sidebarLinks;
    },

    getTheme: state => {
        return state.theme;
    }
}

// MUTATIONS
const mutations = {
    SET_TEXT_ON(state, textOn) {
        state.textOn = textOn;
    },

    SET_SIDEBAR_LINKS(state, sidebarLinks) {
        state.sidebarLinks = sidebarLinks;
    },

    SET_THEME(state, theme) {
        state.theme = theme;
    }
}

// ACTIONS
const actions = {
    setTextOn({ commit }, payload) {
        commit('SET_TEXT_ON', payload);
    },

    setSidebarLinks({ commit }) {
        const links = [
            { id: 1, name: 'Dashboard', icon: 'home', link: '' },
            { id: 2, name: 'Pages', icon: 'file', link: 'pages', sub: [
                { name: 'View Pages', link:'pages', icon: 'folder-open' },
                { name: 'Create', link:'pages/create', icon: 'pencil-alt' },
            ]},
            { id: 3, name: 'Posts', icon: 'file-alt', link: 'posts', sub: [
                { name: 'View Posts', link:'posts', icon: 'folder-open' },
                { name: 'Create', link:'posts/create', icon: 'pencil-alt' },
            ] },
            { id: 4, name: 'Users', icon: 'users', link: 'users' },
            { id: 5, name: 'Settings', icon: 'tools', link: 'settings' },
        ]

        commit('SET_SIDEBAR_LINKS', links);
    },

    setTheme({ commit }, payload) {
        commit('SET_THEME', payload);
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}