<template>
    <h2 class="page-title dark:text-white">{{ pageTitle }}</h2>
</template>

<script>
import { computed } from 'vue';

export default {
    name: 'Title',

    props: {
        title: {
            type: String,
            default: 'Page Title'
        }
    },

    setup(props) {
        const pageTitle = computed(() => {
            const camelTitle = props.title.replace(/([A-Z])/g, ' $1').trim();

            return camelTitle.charAt(0).toUpperCase() + camelTitle.slice(1);
        })

        return {
            pageTitle
        }
    }
}
</script>

