<template>
    <div class="dashboard min-h-screen flex flex-col">
        <Nav />

        <div class="flex flex-1">
            
            <Sidebar />

            <MainContent>
                <Title :title="title.name" />
                <Info />

                <section class="content-section grid gap-6 mb-8 md:grid-cols-2 xl:grid-cols-4">
                    <BoxStat 
                        bg-color="bg-pink-200" 
                        text-color="text-pink-600" 
                        title="Users"
                        stat="1500"
                        icon="users"
                    />

                    <BoxStat 
                        bg-color="bg-blue-200" 
                        text-color="text-blue-600" 
                        title="Pages"
                        stat="10"
                        icon="file"
                    />

                    <BoxStat 
                        bg-color="bg-green-200" 
                        text-color="text-green-600" 
                        title="Posts"
                        stat="20"
                        icon="file-alt"
                    />

                    <BoxStat 
                        bg-color="bg-indigo-200" 
                        text-color="text-indigo-600" 
                        title="Sales"
                        stat="1500"
                        icon="users"
                    />

                </section>

                <section class="content-section overflow-x-auto">
                    <div class="overflow-hidden rounded-lg shadow-xs w-full">

                        <Table :table-data="users" :table-header="tableHeader" />

                        <Pagination />
                    </div>
                    
                </section>
            </MainContent>
        </div>
    </div>
</template>

<script>
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
import { computed, ref } from 'vue';
import Nav from '@/components/Dashboard/Header/Nav';
import Sidebar from '@/components/Dashboard/Sidebar/Sidebar';
import Title from '@/components/Dashboard/Content/Title';
import Info from '@/components/Dashboard/Content/Info';
import MainContent from '@/components/Dashboard/Content/MainContent';
import Table from '@/components/Common/Table';
import Pagination from '@/components/Common/Pagination';
import BoxStat from '@/components/Dashboard/Content/BoxStat';

export default {
    name: 'Dashboard',

    components: {
        Nav,
        Sidebar,
        Title,
        Info,
        MainContent,
        Table,
        Pagination,
        BoxStat
    },

    setup() {
        const route = useRoute();

        const store = useStore();

        const title = route;

        const user = computed(() => { return store.getters['login/getLoginStatus'] });

        const tableHeader = ref([
            {
                name: 'id',
                editable: false
            },
            {
                name: 'name',
                // type: 'Object',
                editable: false
            },
            {
                name: 'image',
                editable: false
            },
            {
                name: 'email',
                editable: false
            },
            {
                name: 'role',
                editable: false
            },
            {
                name: 'status',
                editable: false
            }
        ])

        const users = computed(() => {
            return store.getters['users/getUsers'];
        })

        return {
            title,
            users,
            tableHeader,
            user
        }
    }

}
</script>

