<template>
    <header class="p-4 flex justify-between">
        <div class="logo md:w-1/4 dark:text-white">
            <div class="flex">
                <button @click="hideLinks">
                    <icon v-if="textOn" icon="arrow-left" />
                    <icon v-else icon="arrow-right" />
                </button>

                <h2 class="ml-6 text-lg font-bold text-gray-800 dark:text-white">Logo</h2>
            </div>
        </div>

        <div class="search-box-wrapper md:mr-24 w-1/3 md:w-4/12 relative">
            <search-box></search-box>
        </div>

        <div class="account-links w-1/5 md:w-1/4 flex justify-between md:justify-end md:pr-3">
            <div class="mr-8 flex justify-end">
                <ul class="flex items-center flex-shrink-0 space-x-6">
                    <li>
                        <button @click="changeTheme">
                            <icon v-if="theme === 'light'" icon="moon" :class="['text-purple-700', 'hover:opacity-50']" />
                            <icon v-else icon="sun" :class="['text-purple-300', 'hover:opacity-50']" />
                        </button>
                    </li>
                </ul>
            </div>

            <div class="mr-8 flex items-center justify-end relative">
                <icon @click="toggleMessages" icon="bell" :class="['text-purple-700', 'hover:opacity-50', 'cursor-pointer', 'dark:text-purple-300']" />
                
                <ul :class="(alerts.messages) ? 'transition ease-in duration-1000' : 'hidden'" class="dash-nav-dropdown">
                    <li>
                        <div class="flex items-center">
                            <icon icon="inbox" :class="['fa-fw', 'text-purple-700', 'hover:opacity-50', 'text-sm mr-2']" />
                            <span>Messages</span>
                        </div>

                        <div class="flex">
                            <span class="bg-red-100 px-2 border border-red-200 rounded-lg text-xs flex items-center font-medium">12</span>
                        </div>
                    </li>

                    <li>
                        <div class="flex items-center">
                            <icon icon="bell" :class="['fa-fw', 'text-purple-700', 'hover:opacity-50', 'text-sm mr-2']" />
                            <span>Sales</span>
                        </div>

                        <div class="flex">
                            <span class="bg-red-100 px-2 border border-red-200 rounded-lg text-xs flex items-center font-medium">11</span>
                        </div>
                    </li>

                    <li>
                        <div class="flex items-center">
                            <icon icon="bell" :class="['fa-fw', 'text-purple-700', 'hover:opacity-50', 'text-sm mr-2']" />
                            <span>Alerts</span>
                        </div>

                        <div class="flex">
                            <span class="bg-red-100 px-2 border border-red-200 rounded-lg text-xs flex items-center font-medium">12</span>
                        </div>
                    </li>
                </ul>
            </div>

            <div class="mr-8 flex items-center justify-end relative">

                {{ loggedUser }}

                <img @click="toggleProfile" :src="profileImg" class="object-cover w-8 h-8 rounded-full cursor-pointer" alt="" />

                <ul :class="(alerts.profile) ? 'transition ease-in duration-1000' : 'hidden'" class="dash-nav-dropdown">
                    <li>
                        <div class="flex items-center">
                            <icon icon="user" :class="['fa-fw', 'text-purple-700', 'hover:opacity-50', 'text-sm mr-2']" />
                            <span>Profile</span>
                        </div>
                    </li>

                    <li>
                        <div class="flex items-center">
                            <icon icon="cog" :class="['fa-fw', 'text-purple-700', 'hover:opacity-50', 'text-sm mr-2']" />
                            <span>Settings</span>
                        </div>
                    </li>

                    <li>
                        <div class="flex items-center">
                            <icon icon="sign-out-alt" :class="['fa-fw', 'text-purple-700', 'hover:opacity-50', 'text-sm mr-2']" />
                            <span>Logout</span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </header>
</template>

<script>
import { useStore } from 'vuex';
import { computed, ref, reactive } from 'vue';
import Icon from '@/components/Common/Icon';
import SearchBox from '@/components/Common/SearchBox';

export default {
    name: 'Nav',

    components: {
        Icon,
        SearchBox
    },
    
    setup() {
        const store = useStore();

        const textOn = ref(computed(() => {
            return store.getters['dashboard/textOnStatus'];
        }))
        
        const theme = ref(computed(() => {
            return store.getters['dashboard/getTheme'];
        }));

        const alerts = reactive({
            messages: false,
            profile: false
        })

        const profileImg = computed(() => {
            return require(`@/assets/images/profile/profile.jpeg`);
        })

        const loggedUser = computed(() => {
            return store.getters['login/getLoggedUser'];
        })

        //  METHODS

        const hideLinks = () => {
            const stat = !textOn.value;
            store.dispatch('dashboard/setTextOn', stat);
        }

        const changeTheme = () => {
            if(theme.value === 'light') {
                return store.dispatch('dashboard/setTheme', 'dark');
            }

            return store.dispatch('dashboard/setTheme', 'light')
        }

        const toggleMessages = () => {
            if(alerts.profile) { alerts.profile = false }
            return alerts.messages = !alerts.messages;
        }

        const toggleProfile = () => {
            if(alerts.messages) { alerts.messages = false }
            return alerts.profile = !alerts.profile;
        }

        return {
            hideLinks,
            textOn,
            changeTheme,
            theme,
            alerts,
            toggleMessages,
            toggleProfile,
            profileImg,
            loggedUser
        }
    }
}
</script>

