<template>
    <aside 
        :class="(!textOn) ? 'w-10 md:w-20': 'md:w-1/5'"
        class="sidebar transition-all duration-100"
    >
        <ul class="list-reset flex flex-col">
            <li 
                class="relative px-2 md:px-6 py-3" 
                v-for="link in links" 
                :key="link.id"
            >
                <Link :linkData="link" />
            </li>
        </ul>
    </aside>
</template>

<script>
import { useStore } from 'vuex';
import { computed, onMounted, ref } from 'vue';
import Link from '@/components/Dashboard/Sidebar/Link';

export default {
    name: 'Sidebar',
    components: {
        Link,
    },

    setup() {
        
        const store = useStore();

        const links = computed(() => {
            return store.getters['dashboard/getSidebarLinks'];
        })
        
        // const links = computed( () => {
        //     return [
        //     { id: 1, name: 'Pages', icon: 'home', link: '/pages' },
        // ]})

        const textOn = ref(computed(() => {
            return store.getters['dashboard/textOnStatus'];
        }))

        onMounted(() => {
            /** 
             * Force to hide the link text name (textOn)
            */

            const disableText = () => {
                const wSize = window.innerWidth;

                if(parseInt(wSize) < 500) {
                    store.dispatch('dashboard/setTextOn', false)
                }
            }

            disableText();

            window.addEventListener('resize', () => {
                disableText()
            })
        })


        return {
            links,
            textOn,
        }
    }
}
</script>

