                </label>

                <label class="block" for="content" id="content">
                    <span class="dark:text-white">Content</span>
                    <textarea class="bg-gray-200 input-field mt-2 h-40 dark:bg-white" id="content">

                    </textarea>
                </label>
            </div>
        </div>
    </div>
</template>

<script>
import { useRoute } from 'vue-router';

export default {
    name: 'Create',

