<template>
    <div :class="theme">
        <div class="dark:bg-gray-900">
            <router-view/>
        </div>
    </div>
</template>

<script>
import { useStore } from 'vuex';
import { computed, onMounted, } from 'vue';

export default {
    setup() {
        const store = useStore();

        const theme = computed(() => {
            return store.getters['dashboard/getTheme'];
        })

        onMounted(() => {
            store.dispatch('dashboard/setSidebarLinks');
            store.dispatch('users/fetchUsers');
            store.dispatch('pages/fetchPages');
            store.dispatch('posts/fetchPosts');
        })

        return {
            theme
        }
    }
}
</script>

