// STATE
const state = {
    users: []
}

// GETTERS
const getters = {
    getUsers: state => {
        return state.users;
    }
}

// MUTATIONS
const mutations = {
    SET_USERS(state, users) {
        state.users = users;
    }
}

// ACTIONS
const actions = {
    fetchUsers({ commit }) {
        let data = [];

        for(let i = 1; i < 5; i++) {
            const user = {
                id: i,
                name: `User ${i}`,
                username: `user${i}`,
                password: `password-${i}`,
                email: `user-${i}@dev.local`,
                role: 0,
                image: 'profile.jpeg',
                status: 'Active'
            }
            data.push(user);
        }

        commit('SET_USERS', data);

        // try {
        //     fetch('https://next.json-generator.com/api/json/get/41Bgu3I5F')
        //         .then(results => { return results.json()})
        //         .then(data => {
        //             console.log(data)
        //             commit('SET_USERS', data);
        //         })
        // } catch(error) {
        //     console.log(error)
        // }

    }

}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}