import { createRouter, createWebHistory } from 'vue-router'
// import Home from '../views/Home.vue'
import Dashboard from '../views/Dashboard/Dashboard.vue'
import Create from '../views/Dashboard/Create.vue';
import store from '../store';

const routes = [
    {
        path: '/',
        name: 'dashboard',
        component: Dashboard,
        beforeEnter: async () => {
            await store.dispatch('login/checkToken');
        }
    },
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
        path: '/pages',
        children: [
            { path: 'create', name: 'createPage', component: Create }
        ],
        name: 'pages',
        component: () => import(/* webpackChunkName: "pages" */ '../views/Dashboard/Pages.vue'),
        beforeEnter: async () => {
            await store.dispatch('login/checkToken');
        }
    },
    {
        path: '/users',
        name: 'users',
        component: () => import(/* webpackChunkName: "users" */ '../views/Dashboard/Users.vue'),
        beforeEnter: async () => {
            await store.dispatch('login/checkToken');
        }
    },
    {
        path: '/posts',
        children: [
            { path: 'create', name: 'createPost', component: Create }
        ],
        name: 'posts',
        component: () => import(/* webpackChunkName: "users" */ '../views/Dashboard/Posts.vue'),
        beforeEnter: async () => {
            await store.dispatch('login/checkToken');
        }
    },
    {
        path: '/settings',
        name: 'settings',
        component: () => import(/* webpackChunkName: "login" */ '../views/Dashboard/Settings.vue'),
        beforeEnter: async () => {
            await store.dispatch('login/checkToken');
        }
    },
    {
        path: '/login',
        name: 'login',
        component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue')
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router
