<template>
    <!-- NO SUBMENU LINK -->
    <router-link v-if="!linkData.sub" :to="`/${ linkData.link }`" class="text-gray-700 hover:text-purple-600 transition-colors duration-300 flex items-center dark:text-gray-600 dark:hover:text-gray-200">
        <span 
            class="active-link hidden absolute inset-y-0 left-0 w-1 bg-purple-600 rounded-tr-lg rounded-br-lg" 
            aria-hidden="true"
        >
        </span>
        <icon :icon="linkData.icon" :class="['fa-fw']" />

        <div 
            class="sidebar-link md:ml-4 flex justify-between flex-auto"
        >
            <span :class="(!textOn) ? 'hidden': ''">
                {{ linkData.name }}
            </span>

            <span v-if="linkData.sub" @click="toggleSubmenu">
                <icon icon="plus" :class="['fa-fw', 'text-xs', 'text-gray-600']" />
            </span>
        </div>
    </router-link>

    <!-- WITH SUBMENU LINKS -->
    <router-link v-else @click.prevent="toggleSubmenu" :to="`/${ linkData.link }`" class="text-gray-700 hover:text-purple-600 transition-colors duration-300 flex items-center dark:text-gray-600 dark:hover:text-gray-200">
        <span 
            class="active-link hidden absolute inset-y-0 left-0 w-1 bg-purple-600 rounded-tr-lg rounded-br-lg" 
            aria-hidden="true"
        >
        </span>
        <icon :icon="linkData.icon" :class="['fa-fw']" />

        <div 
            class="sidebar-link md:ml-4 flex justify-between flex-auto"
        >
            <span :class="(!textOn) ? 'hidden': ''">
                {{ linkData.name }}
            </span>

            <span>
                <icon icon="plus" :class="['fa-fw', 'text-xs', 'text-gray-600']" />
            </span>
        </div>
    </router-link>

    <!-- SUB-MENU -->

    <ul v-if="linkData.sub && showSubmenu" class="submenu p-2 mt-2 space-y-2 overflow-hidden text-sm font-medium  rounded-md shadow-inner bg-gray-50 dark:bg-gray-800">
        <li v-for="(sub, i) in linkData.sub" :key="i" class="px-2 py-1 transition-colors duration-150 text-gray-500 hover:text-purple-600 dark:text-gray-600 dark:hover:text-gray-200">
            <router-link :to="`/${sub.link}`" class="w-full flex items-center justify-center">
                <icon :icon="sub.icon" :class="['fa-fw', 'text-xs', 'text-gray-600']" />

                <div 
                    :class="(!textOn) ? 'hidden': ''"
                    class="sidebar-link md:ml-2 flex justify-between flex-auto"
                >
                    <span>
                        {{ sub.name }}
                    </span>
                </div>
            </router-link>
        </li>
    </ul>
</template>

<script>
import { useStore } from 'vuex';
import { computed, ref } from 'vue';
import Icon from '@/components/Common/Icon';

export default {
    name: 'Link',
    components: {
        Icon
    },
    emits: ['togglesubmenu'],
    props: {
        linkData: {
            type: Object,
            default() {
                return { id: 1, name: 'Pages', icon: 'home', link: '/pages' }
            } 
        }
    },

    setup() {
        const store = useStore();

        const textOn = ref(computed(() => {
            return store.getters['dashboard/textOnStatus'];
        }))

        const showSubmenu = ref(false);

        const toggleSubmenu = () => {
            showSubmenu.value = !showSubmenu.value;
        }

        return {
            textOn,
            toggleSubmenu,
            showSubmenu
        }
    }

}
</script>

<style lang="scss" scoped>
    .router-link-active {
        @apply text-purple-600;

        .active-link {
            display: block;
        } 
    }
</style>