<template>
    <div class="overflow-x-auto w-full">
        <table class="w-full whitespace-no-wrap">
            <thead class="bg-gray-300 dark:border-indigo-900">
                <tr class="text-xs font-semibold tracking-wide text-left text-gray-800 uppercase bg-gray-200 dark:text-gray-100">
                    <th class="px-4 py-3 dark:bg-indigo-900" v-for="(header, i) in tableHeader" :key="i">
                        <span v-if="(header.name !== 'image')">
                            {{ header.name }}
                        </span>

                        <span v-else class="hidden"></span>
                    </th>
                </tr>
            </thead>

            <tbody class="bg-white divide-y dark:bg-gray-900">
                <tr v-for="(data, dataIndex) in tableData" :key="dataIndex" class="text-gray-700 dark:text-gray-400 dark:border-gray-800 dark:bg-gray-900">
                    <td 
                        v-for="(header, headerIndex) in tableHeader" 
                        :key="headerIndex"
                        :class="(header.name === 'name') ? 'flex items-center' : ''"
                        class="px-4 py-3 dark:text-white" 
                    >
                        <span v-show="header.name === 'name'">
                            <img
                                v-if="data.image != null" 
                                :src="require(`@/assets/images/profile/${data.image}`)" 
                                class="mr-4 object-cover w-8 h-8 rounded-full cursor-pointer" 
                                alt="" 
                            />
                        </span>

                        <div v-if="header.name != 'image'">
                            <span v-if="header.type === 'Object'">
                                <ul class="flex">
                                    <li v-for="(name, i) in data[header.name]" :key="i" class="mr-2">
                                        {{ name }}
                                    </li>
                                </ul>
                            </span>

                            <span v-else>
                                {{ data[header.name] }}
                            </span>
                        </div>
                        <div v-else class="hidden"></div>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>

export default {
    name: "Table",
    props: {
        tableData: {
            type: Object,
            default: () => {}
        },

        tableHeader: {
            type: Object,
            default: () => {}
        }
    },

    setup() {

        const joinData = (x, y) => {
            return `${x}.${y}`;
        }

        return {
            joinData,
        }
    }
}
</script>

