import { BASE_URL } from "../../../config";
import axios from 'axios';
import router from '../../../src/router';

// STATE
const state = {
    user: [],
    loginStatus: {
        error: false,
        success: false,
        isLogged: false,
    }
}

// GETTERS
const getters = {
    getLoginStatus: state => {
        return state.loginStatus;
    },

    getLoggedUser: state => {
        return state.user;
    }
} 

// MUTATIONS
const mutations = {
    SET_LOGINSTATUS_ERROR(state, error) {
        state.loginStatus.error = error
    },

    SET_LOGINSTATUS_EMPTY(state, empty) {
        state.loginStatus.empty = empty
    },

    SET_LOGINSTATUS_SUCCESS(state, success) {
        state.loginStatus.success = success
    },

    SET_LOGINSTATUS_ISLOGGED(state, isLogged) {
        state.loginStatus.isLogged = isLogged
    },

    SET_USER(state, user) {
        state.user = user;
    }
}

// ACTIONS
const actions = {
    isUserLogged({ dispatch }) {
        if(localStorage.getItem('token') === null || localStorage.getItem === undefined) {
            router.push('/login', () => {});
        } else {
            dispatch('checkToken');
        }

    },

    async userLogin({ commit }, payload) {
        const { email, password } = payload;

        // CHECK IF EMPTY

        if(email.length === 0 || password.length === 0) {
            return commit('SET_LOGINSTATUS_EMPTY', true);
        }

        try {
            const user = await axios.post(`${BASE_URL}/api/auth`, { email, password});

            if(user.status === 200) {
                commit('SET_LOGINSTATUS_ERROR', false);
                commit('SET_LOGINSTATUS_EMPTY', false);
                commit('SET_LOGINSTATUS_SUCCESS', true);
                await commit('SET_LOGINSTATUS_ISLOGGED', true);

                localStorage.setItem('token', user.data.token);

                router.push({ name: 'dashboard' });

                // setTimeout(() => {
                //     router.push({ name: 'dashboard' });
                // }, 2000);

            } else {
                commit('SET_LOGINSTATUS_ERROR', true);
                commit('SET_LOGINSTATUS_EMPTY', false);
                commit('SET_LOGINSTATUS_ISLOGGED', false);
            }   

        } catch(error) {
            commit('SET_LOGINSTATUS_ERROR', true);
            commit('SET_LOGINSTATUS_EMPTY', false);
            commit('SET_LOGINSTATUS_ISLOGGED', false);
        }
    },

    async checkToken({ commit }) {
        try {
            await axios.get(`${BASE_URL}/api/auth`, { headers: { token: localStorage.getItem('token')}})
            .then(res => {

                const payload = {
                    id: res.data.user.id,
                    name: res.data.user.name,
                    email: res.data.user.email
                };
               
                commit('SET_USER', payload);
                commit('SET_LOGINSTATUS_ISLOGGED', true);
                // router.push('/', () => {});
                // router.push('/').catch(err => {
                //     console.log(err);
                // });
            })
            .catch( (error) => {
                // dispatch('setAlertMsg', `${error.response.data.error}`);
                console.log(error)
                commit('SET_LOGINSTATUS_ISLOGGED', false);
                router.push('/login', () => {});
            }) 
        } catch(error) {
            commit('SET_LOGINSTATUS_ISLOGGED', false);
            console.log(error);
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}